@font-face {
  font-family: "Inter";
  src:url(/mesto-react/static/media/Inter-Black.661569af.woff2), url(/mesto-react/static/media/Inter-Black.d0b121f3.woff);
  font-weight: 900;
}
@font-face {
  font-family: "Inter";
  src:
    url(/mesto-react/static/media/Inter-Medium.75db5319.woff2), url(/mesto-react/static/media/Inter-Medium.c0638bea.woff);
  font-weight: 500;
}
@font-face {
  font-family: "Inter";
  src:
    url(/mesto-react/static/media/Inter-Regular.dc131113.woff2), url(/mesto-react/static/media/Inter-Regular.3ac83020.woff);
  font-weight: 400;
