.page {
  position: absolute;
  min-width: 100vw;
  background-color: #000;
  color: #fff;
  font-size: 16px;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
} 
.page__container {
  max-width: 880px;
  min-height: 100vh;
  margin: 0 auto;
}

@media screen and (max-width: 900px) {
  .page__container {
    margin: 0 19px;
  }
}

@media screen and (max-width: 320px) {
  .page__container {
    margin: 0 auto;
  }
}
.header {
  width: 100%;
  margin-top: 45px;
  border-bottom:  1px solid rgba(84, 84, 84, .7);
  position: relative;
}

.header::after {
  display: none;
  position: absolute;
  left: -18px;
  content: "";
  width: 318px;
  border-bottom:  1px solid rgba(84, 84, 84, .7);
  box-sizing: border-box;
  overflow: hidden;
}

@media screen and (max-width: 520px) {
  .header {
    margin-top: 28px;
  }
}

@media screen and (max-width: 320px) {
  .header {
    max-width: 320px;
    border-bottom: none;
  }
  .header::after {
    display: block;
  }
}

.header__logo {
  padding-bottom: 34px;
}

@media screen and (max-width: 520px) {
  .header__logo {
    width: 103px;
    margin-left: 10px;
    padding-bottom: 28px;
  }
}

.content {
  width: 100%;
  margin-top: 40px;
}

.profile {
  width: 100%;
  min-height: 120px;
  display: grid;
  grid-template-columns: 120px auto 150px;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
}

@media screen and (max-width: 900px) {
  .profile {
    grid-template-columns: none;
  }
}

@media screen and (max-width: 320px) {
  .profile {
    grid-gap: 28px;
    gap: 28px;
  }
}

.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  background-size: cover;
  background-position: center;
  transition: .5s linear;
}

@media screen and (max-width: 900px) {
  .profile__avatar {
    justify-self: center;
  }
}

.profile__avatar:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

.profile__info {
  max-width: 336px;
  display: grid;
}

@media screen and (max-width: 900px) {
  .profile__info {
    justify-self: center;
  }
}

.profile__title {
  margin: 0;
  padding: 0;
  max-width: 294px;
  font-weight: 500;
  font-size: 2.625rem;
  line-height: 3rem;
  /* лишний текст превращает в точки */
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 619px) {
  .profile__title {
    max-width: 248px;
    font-size: 2rem;
    line-height: 2.5rem;
  }
}

@media screen and (max-width: 520px) {
  .profile__title {
    font-size: 1.6875rem;
    line-height: 2.0625rem;
  }
}

.profile__edit-button {
  width: 24px;
  height: 24px;
  align-self: center;
  background-color: #000;
  border: 1px solid #fff;
  background-size: 10px;
  background-image: url(/mesto-react/static/media/Edit_Button.58afbe73.svg);
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  padding: 0;
  transition: 0.5s ease-in;
}

.profile__edit-button:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  opacity: .6;
  transition: 0.5s ease-in-out;
}

@media screen and (max-width: 320px) {
  .profile__edit-button {
    width: 18px;
    height: 18px;
  }
}

.profile__heading {
  display: flex;
  justify-content: space-between;
  grid-gap: 18px;
  gap: 18px;
}

@media screen and (max-width: 900px) {
  .profile__heading {
    grid-gap: 10px;
    gap: 10px;
  }
}

.profile__subtitle {
  margin: 16px 0 0 0;
  padding: 0;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.375rem;
  /* лишний текст превращает в точки */
  max-width: 336px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 900px) {
  .profile__subtitle {
    text-align: center;
  }
}

@media screen and (max-width: 619px) {
  .profile__subtitle {
    max-width: 282px;
    font-size: 1.1rem;
    line-height: 1.125rem;
  }
}

@media screen and (max-width: 520px) {
  .profile__subtitle {
    font-size: 0.875rem;
    line-height: 1.0625rem;
  }
}


@media screen and (max-width: 320px) {
  .profile__subtitle {
    margin: 7px 0 0 0;
  }
}

.profile__add-button {
  width: 150px;
  height: 50px;
  background-color: #000;
  border: 2px solid #fff;
  border-radius: 2px;
  background-size: 22px;
  background-image: url(/mesto-react/static/media/Add_Button.bdfb8ebb.svg);
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  padding: 0;
  transition: 0.5s ease-in;
}

.profile__add-button:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  opacity: .6;
  transition: 0.5s ease-in-out;
}

@media screen and (max-width: 900px) {
  .profile__add-button {
    width: 590px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 635px) {
  .profile__add-button {
    width: 582px;
  }
}

@media screen and (max-width: 619px) {
  .profile__add-button {
    width: 282px;
  }
}

@media screen and (max-width: 320px) {
  .profile__add-button {
    margin-top: 6px;
    background-size: 16px;
    background-image: url(/mesto-react/static/media/Vector.c280e852.svg);
  }
}

.profile__wrapper-relative {
  margin: 0;
  padding: 0;
  position: relative;
}

@media screen and (max-width: 680px) {
  .profile__wrapper-relative {
    width: 240px;
    height: 240px;
  }
}

@media screen and (max-width: 440px) {
  .profile__wrapper-relative {
    width: 120px;
    height: 120px;
  }
}
.profile__edit-avatar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 120px;
  height: 120px;
  border-style: none;
  border-radius: 50%;
  opacity: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.8);
}

.profile__edit-avatar:hover {
  opacity: 1;
  transition: 0.5s ease-in-out;
}
.elements {
  width: 100%;
  margin: 50px auto 66px;
}

@media screen and (max-width: 900px) {
  .elements {
    width: 581px;
  }
}

@media screen and (max-width: 619px) {
  .elements {
    width: 282px;
  }
}

@media screen and (max-width: 320px) {
  .elements {
    margin: 36px auto 46px;
  }
}

.elements__list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 17px;
  gap: 17px;
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 320px) {
  .elements__list  {
    grid-gap: 21px;
    gap: 21px;
  }
}

.element {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #fff;
  border-radius: 10px;
  transition: .8s ease-in;
}

@media screen and (max-width: 320px) {
  .element {
    width: 282px;
    border: 0;
  }
}

.element:hover{
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.element__image {
  display: block;
  width: 282px;
  height: 282px;
  border-radius: 10px 10px 0 0;
  object-fit: cover;
  background-size: cover;
}

.element__image:hover {
  cursor: pointer;
}

.element__group {
  display: flex;
  justify-content: space-between;
  color: #000;
  margin: 25px 15px 31px 21px;
}

@media screen and (max-width: 320px) {
  .element__group {
    margin: 21px 21px 25px 21px;
  }
}

.element__title{
  padding: 0;
  margin: 0;
  font-weight: 900;
  font-size: 1.5rem;
  line-height: 1.8125rem;
  /* лишний текст превращает в точки */
  display: block;
  width: 220px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 620px) {
  .element__title {
    font-size: 1.5rem;
    line-height: 1.8125rem;
  }
}

.element__like-button {
  width: 22px;
  height: 19px;
  border: none;
  align-self: center;
  background-color: transparent;
  background-image: url(/mesto-react/static/media/like.cb5f1643.svg);
  cursor: pointer;
  transition: .5s ease-in;
}

.element__like-button:hover {
  background-image: url(/mesto-react/static/media/Union.933106ea.svg);
  opacity: .5;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.element__like-button:active {
  background-image: url(/mesto-react/static/media/Union.933106ea.svg);
  opacity: 1;
}

.element__like-button_active {
  background-image: url(/mesto-react/static/media/Union.933106ea.svg);
  opacity: 1;
}

.element__count-like {
  margin: 0;
  font-weight: 400;
  font-size: .8125rem;
  line-height: 16px;
  text-align: center;
  color: #000;
  margin-top: .1875rem;
}
.element__trash {
  width: 18px;
  height: 19px;
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  background-color: transparent;
  background-image: url(/mesto-react/static/media/trash.48bb334e.svg);
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 1;
}

.element__trash:hover {
  opacity: .5;
  transition: .5s ease-in;
  background-color: #000;
  border-radius: 10%;
  padding: 5px;
}

.footer {
  display: flex;
  width: 100%;
  margin: 0 0 60px 0;
}

@media screen and (max-width: 420px) {
  .footer {
    margin: 0 0 36px 0;
  }
}

.footer__title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.375rem;
  color: #545454;
}

@media screen and (max-width: 420px) {
  .footer__title {
    font-size: .875rem;
    line-height: 1.0625rem;
  }
}

.popup {
  background: rgba(0, 0, 0, .5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  transition:visibility .5s, opacity .5s linear;
}

.popup_img {
  transition:visibility .0s, opacity .0s linear;
}
.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  position: relative;
  width: 430px;
  border: 0;
  border-radius: 10px;
  background-color: #fff;
  box-sizing: border-box;
  z-index: 4;
}

@media screen and (max-width: 768px) {
  .popup__container {
    width: 282px;
  }
}

.popup__content {
  margin: 34px auto 37px;
  padding: 0;
  border: 0;
  max-width: 358px;
}

@media screen and (max-width: 768px) {
  .popup__content {
    margin: 25px auto;
    max-width: 238px;
  }
}

.popup__image-container {
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  max-width: 75vw;
  height: 75vh;
}

.popup__image {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}

.popup__image-title {
  margin: 0;
  padding: 10px 0 0 0;
  font-weight: 400;
  font-size: .75rem;
  line-height: .9375rem;
  max-width: 291px;
  color: #fff;
}

.popup__close-button {
  width: 32px;
  height: 32px;
  z-index: 5;
  position: absolute;
  right: -40px;
  top: -40px;
  background-color: transparent;
  background: url(/mesto-react/static/media/CloseIcon.4c6a2169.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}

.popup__close-button:hover {
  opacity: .6;
  transition: 0.5s ease-in-out;
}

.popup__close-button:focus {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  outline: 0;
}

@media screen and (max-width: 768px) {
  .popup__close-button {
    width: 20px;
    height: 20px;
    right: 0;
    top: -36px;
  }
}

.popup__title {
  color: #000;
  font-weight: 900;
  font-size: 1.5rem;
  line-height: 1.8125rem;
  margin: 0 0 54px 0;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .popup__title {
    font-size: 1.125rem;
    line-height: 1.375rem;
    margin: 0 0 75px 0;
  }
}

.popup__save {
  width: 100%;
  height: 50px;
  color: #fff;
  background-color: #000;
  text-align: center;
  border: 0;
  border-radius: 2px;
  padding: 0;
  margin: 0;

  box-sizing: border-box;

  font-size: 1.125rem;
  line-height: 1.375rem;
  cursor: pointer;
}

.popup__save:hover {
  opacity: .8;
}

@media screen and (max-width: 768px) {
  .popup__save {
    font-size: .875rem;
    line-height: 1.0625rem;
    height: 46px;
    margin-top: 15px;
  }
}

.popup__label {
  display: block;
  margin-bottom: 30px;
}

